@use '../additional/mixins' as *;

.tariffs {
  background: url('../img/tariffs-bg.png') no-repeat center / cover;

  &__title {
    margin-bottom: 6rem;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      @include adaptivValue('width', 775, 575, 1165, 991);
      height: 1px;
      background-color: var(--clr-blue);
      top: 23px;
      left: 250px;
    }
  }

  &__wrapp {
    display: flex;
    justify-content: space-between;
    gap: 4rem;
  }

  &__block {
    border-radius: 25px;
    padding: 2rem 3rem 5rem 2rem;
    max-width: 36rem;
    height: fit-content;
    width: 100%;
    position: relative;


    &:first-child {
      border: 1px solid #cddfe7;
      background-color: var(--clr-white);

      .tariffs__heading {
        color: #06141a;
      }

      .tariffs__item::before {
        background-color: var(--clr-black);
      }
      .tariffs__count {
        color: var(--clr-black);
      }
    }

    &:nth-child(2) {
      margin-top: 50px;
      background-color: #1f3742;
      color: #e2e2e2;

      &::before {
        content: '';
        position: absolute;
        top: -128px;
        right: 165px;
        background-color: var(--clr-blue);
        width: 1px;
        height: 100px;
      }

      .tariffs__item::before {
        background-color: #e2e2e2;
      }
    }

    &:last-child {
      margin-top: 130px;
      color: var(--clr-white);
      background-image: url('../img/bg.png');
      background-position: right -670px top -75px;
      background-repeat: no-repeat;
      padding: 0;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: -208px;
        right: 140px;
        background-color: var(--clr-blue);
        width: 1px;
        height: 150px;
      }

      .tariffs__heading::after {
        background-color: var(--clr-white);
      }

      .tariffs__list {
        margin-bottom: 3rem;
      }

      .tariffs__item {
        color: var(--clr-white);
      }

      .tariffs__price {
        color: var(--clr-white);

        span {
          color: var(--clr-white);
        }
      }

      .tariffs__btn {
        background-color: var(--clr-white);
        color: var(--clr-black);
      }
    }
  }

  &__name {
    font-family: var(--ff-2);
    font-weight: var(--fw-500);
    width: fit-content;
    margin-bottom: 2rem;
    background-color: #27B6F3;
    border-radius: 4px;
    padding: 3px;
    text-transform: uppercase;
    color: var(--clr-white);
  }

  &__layer {
    padding: 2rem 3rem 5rem 2rem;
    isolation: isolate;
    border-radius: 25px;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: 25px;
      z-index: -1;
      opacity: .67;
      background: linear-gradient(0deg, rgba(20, 49, 64, 0.91), rgba(20, 49, 64, 0.91)), linear-gradient(180deg, #27B6F3 0%, #39B9EF 53.12%, #27B6F3 100%);
    }

    &::after {
      content: '';
      border-radius: 25px;
      position: absolute;
      inset: 0;
      z-index: -1;
      backdrop-filter: blur(30px);
      -webkit-backdrop-filter: blur(30px);
    }
  }

  &__tag {
    text-transform: uppercase;
    font-size: var(--fz-18);
    color: #b7b7b7;
    font-weight: var(--fw-300);
    margin-bottom: 2rem;
  }

  &__heading {
    margin-bottom: 3.5rem;
    max-width: 27.5rem;
    position: relative;
    padding-bottom: 2rem;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: var(--clr-blue);
      border-radius: 33px;
      width: 85px;
      height: 5px;
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-bottom: 1.5rem;
  }
  &__item {
    line-height: 1.3em;
    padding-left: 15px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0.5em;
      display: block;
      width: 2px;
      height: 2px;
      border-radius: 100vmax;
      background-color: var(--clr-white);
    }

    &.gradient-1 {
      color: #b7bec1;

      &:before {
        background-color: #b7bec1;
      }
    }

    &.gradient-2 {
      color: #b7bec1;
      opacity: 0.4;

      &:before {
        background-color: #b7bec1;
      }
    }
  }
  &__price {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 3rem;
    font-size: var(--fz-28);
    color: #5d686c;
    margin-top: auto;

    span {
      font-size: var(--fz-50);
      font-weight: var(--fw-700);
      color: #06141a;
    }
  }

  &__addition {
    margin-bottom: 4.5rem;
    padding: 1.2rem 2rem;
    background: radial-gradient(
      167.5% 964.64% at 32.06% -55%,
      #1f5d77 0%,
      #2880a5 19.29%,
      #2d7c9d 42.53%,
      #1f5d77 100%
    );
    border-radius: 11px;
    line-height: 1.3em;
    border-bottom: 5px solid;
    border-image: linear-gradient(
      91.62deg,
      #1f5d77 0.23%,
      #2880a5 26.11%,
      #2d7c9d 78.4%,
      #1f5d77 103.74%
    );
    overflow: hidden;
    position: relative;

    &::before {
      content: url('../img/svg/icons/star.svg');
      position: absolute;
      top: 0;
      left: -40px;
      width: 125px;
      height: 135px;
    }
  }

  &__count {
    font-family: 'Bebas Neue', sans-serif;
    display: block;
    text-transform: uppercase;
    margin-bottom: 3rem;
    font-size: 36px;
    text-align: center;
    color: #daf4ff;
  }

  &__btn {
    margin-inline: auto;
    background-color: #27b6f3;
  }
}


@include breakpoint-down(tablet-wide) {

  .tariffs {

    &__title {
      text-align: center;
      &::after {
        display: none;
      }
    }

    &__block {
      margin: 0 !important;
      &::before {
        display: none;
      }
    }

    &__wrapp {
      flex-wrap: wrap;
      justify-content: center;
      gap: 2rem;

    }
  }
}

@include breakpoint-down(phablet) {

  .tariffs {

    &__block {
      padding: 3rem 2.5rem;
    }
    &__title {
      margin-bottom: 3rem;
    }
    &__tag {
      margin-bottom: 2rem;
    }

    &__list {
      margin-bottom: 3rem;
    }

    &__name {
      position: absolute;
      top: 25px;
      right: 25px;
      margin: 0;
    }

    &__heading {
      font-size: 28px;
    }

    &__count {
      margin-bottom: 2rem;
    }

    &__addition {
      margin-bottom: 2rem;
    }

    &__btn {
      background-color: var(--clr-light-blue) !important;
      max-width: 20rem;
      margin-inline: auto;
      font-size: 12px;
      color: var(--clr-white) !important;
    }
  }
}
