@use '../additional/mixins' as *;

.promises {
  background-image: url('../img/promises-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100vh;

  &__inner {
    position: relative;
    height: 100%;
  }
  &__title {
    text-align: center;
    max-width: 67rem;
    margin-inline: auto;
    color: var(--clr-white);
    padding-bottom: 1rem;
    position: relative;

    span {
      display: inline;
      color: var(--clr-light-blue);
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      max-width: 57.6rem;
      width: 100%;
      height: 3px;
      background-color: var(--clr-blue);
    }
  }

  &__item {
    position: absolute;
    max-width: 36rem;

    background-color: #f8f8f8;
    border-radius: 16px;
    font-size: var(--fz-24);
    font-weight: var(--fw-300);
    color: #606061;
    padding: 1.5rem 3rem 1.5rem 5rem;

    &:nth-child(1) {
      transform: rotate(-5.2deg);
      top: 100px;
      left: 0;
    }

    &:nth-child(2) {
      transform: rotate(-9.48deg);
      top: 100px;
      right: 0;
    }

    &:nth-child(3) {
      transform: rotate(8.57deg);
      top: 300px;
      left: 0;
    }

    &:nth-child(4) {
      transform: rotate(7.26deg);
      top: 370px;
      right: 0;
    }

    &:nth-child(5) {
      opacity: 0.8;
      filter: blur(3.61996px);
      transform: rotate(-18.62deg) scale(0.6);
      left: -250px;
    }

    &:last-child {
      opacity: 0.8;
      filter: blur(3.61996px);
      transform: rotate(26.89deg) scale(0.6);
      top: 250px;
      right: -200px;
    }
  }
}

@include breakpoint-down(desktop-wide) {
  .promises {
    height: 80vh;
    &__item:nth-child(1) {
      top: 0;
      left: 0;
    }
    &__item:nth-child(2) {
      top: 0;
      right: 0;
    }
    &__item:nth-child(3) {
      top: 200px;
      left: 0;
    }
    &__item:nth-child(4) {
      top: 300px;
      right: 0;
    }
  }
}

@include breakpoint-down(desktop) {
  .promises {
    &__item {
      &:nth-child(1) {
        left: 0;
      }
      &:nth-child(2) {
        right: 0;
      }
      &:nth-child(3) {
        top: 200px;
        left: 0;
      }
      &:nth-child(4) {
        top: 300px;
        right: 0;
      }
      &:nth-child(5),
      &:nth-child(6) {
        display: none;
      }
    }
  }
}

@include breakpoint-down(tablet-wide) {

  .promises {
    &__item {

      &:nth-child(1) {
        left: 0;
      }
      &:nth-child(2) {
        right: 0;
      }
      &:nth-child(3) {
        left: 0;
      }
      &:nth-child(4) {
        right:0;
      }
    }
  }
}

@include breakpoint-down(tablet) {
  .promises {
    &__item {
      padding: 1rem 1rem 1rem 2rem;
      font-size: 1.4rem;
      border-radius: 7px;

      &:nth-child(1) {
        top: 100px;
        left: 0;
      }
      &:nth-child(2) {
        top: 100px;
        right: 0;
      }
      &:nth-child(3) {
        left: 0;
      }
      &:nth-child(4) {
        top: 200px;
        right:0;
      }
    }
  }
}

@include breakpoint-down(tablet-small) {
  .promises {
    height: 420px;
    &__title {
      &::after {
        max-width: 215px;
        transform: rotate(-1.07deg);
        margin-inline: auto;
        left: 0;
        right: 0;
      }
    }

    &__item {
      padding: 7px 7px 7px 10px;
      font-size: 1rem;
      max-width: 15.5rem;


    }
  }
}

@include breakpoint-down(phablet) {
  .promises {
    padding-top: 25px;
    background-image: url('../img/promises-sm-bg.png');
    background-position: top  center;
    background-size: cover;

    &__title {
      font-size: 1.8rem;
      &::after {
        transform: rotate(-1.07deg);
        margin-inline: auto;
        left: 0;
        right: 0;
      }
    }

    &__item {
      &:nth-child(1) {
        top: 20px;
      }
      &:nth-child(2) {
        top: 20px;
      }
      &:nth-child(3) {
        top: 175px;
      }
      &:nth-child(4) {
        top: 240px;
      }
    }
  }
}
