@use '../additional/mixins' as *;

.facts {
  background-image: url('../img/facts-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #11191D;
  overflow: hidden;

  &__title {
    margin-bottom: 9rem;
    text-align: center;
  }
  &__list {
    display: grid;
    grid-template-columns: 42rem 42rem;
    justify-content: center;
    justify-items: center;
    margin-bottom: 7rem;
    gap: 7rem 3.5rem;
    color: var(--clr-white);
  }
  &__item {
    max-width: 33.5rem;
    position: relative;

    &:nth-child(even) {
      margin-left: auto;
    }

    &::before {
      content: attr(count);
      position: absolute;
      top: 0;
      left: -100px;
      width: 60px;
      height: 60px;
      font-size: var(--fz-24);
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid var(--clr-white);
      border-bottom: none;
      border-radius: 50px;
      color: var(--clr-white);
      background: -webkit-linear-gradient(#eee, #333);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: -100px;
      width: 60px;
      height: 60px;
      border-radius: 50px;
    }
  }

  &__heading {
    margin-bottom: 2rem;
  }
  &__text {
    color: #D3D3D3;
    line-height: 1.3em;

    .indent {
      display: none;
    }

    &:not(:last-of-type) {
      margin-bottom: 1rem;
    }
  }
  &__btn {
    margin-inline: auto;
    margin-bottom: 9rem;
  }

  &__line {
    padding: 13px 0;
    text-transform: uppercase;
    color: var(--clr-white);
    position: absolute;
    bottom: 50px;
    right: 0;
    white-space: nowrap;

    &--first {
      transform: rotate(-4.96deg);
      background-color: var(--clr-dark-blue);
    }

    &--last {
      transform: rotate(2.86deg);
      background-color: var(--clr-light-blue);
    }
  }

}

@include breakpoint-down(desktop-wide) {
  .facts {
    &__line {
      display: none;
    }

    &__btn {
      margin-block: 0;
    }
  }
}



@include breakpoint-down(tablet-wide) {

  .facts__list {
    grid-template-columns: 32rem 32rem;
    gap: 4rem 5rem;
  }
  .facts__item {
    &:nth-child(even) {
      margin: 0;
    }
  }
  .facts {
    &__item::before {
      left: -50px;
      width: 30px;
      height: 30px;
      font-size: var(--fz-18);
    }
  }
}

@include breakpoint-down(tablet) {

  .facts__heading {
    padding-left: 4rem;

    br {
      display: none;
    }
  }
  .facts__list {
    grid-template-columns: 45rem;
    justify-content: center;
    grid-template-columns: 1fr;
    gap: 4rem;
    margin-bottom: 4rem;
    padding: 0;
  }

  .facts__item {
    justify-self: center;
    max-width: 100%;

    &::before,
    &::after {
      top: -2px;
      left: 0;
      width: 30px;
      height: 30px;
      font-size: 14px;
    }
  }
}



@include breakpoint-down(phablet) {
  .facts {
    background-image: url('../img/facts-sm-bg.png');

    &__title {
      margin-bottom: 5rem;
    }
  }
}
