@use '../additional/mixins' as *;

.program {
  background-image: url('../img/program-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__title {
    margin-bottom: 10rem;
    text-align: center;
    font-size: var(--fz-64);
  }
}

.program-part {
  display: flex;
  flex-direction: column;
  gap: 6rem;

  &__dropdown {
    flex-grow: 1;
    position: relative;
    padding-top: 11rem;

    & [data-close-spoller] {
      display: block;
      position: absolute;
      top: min(20%, 37px);
      left: 0;
      right: 0;
      margin: 0 auto;
      width: 60px;
      height: 35px;
      font-size: 0;
      z-index: 1;
    }
    &::before,
    &::after {
      content: '';
      position: absolute;
      top: min(20%, 55px);
      left: 0;
      right: 0;
      margin: 0 auto;
      height: 3px;
      width: 50px;
      background-image: linear-gradient(to right, white, transparent, white);
      background-size: 200%;
    }

    &::before {
      background-position: left;
      rotate: 45deg;
      left: 34px;
    }

    &::after {
      background-position: right;
      rotate: -45deg;
      right: 34px;
    }
  }

  &__wrapp {
    display: flex;
    flex-wrap: wrap;
    gap: 0 8rem;
    position: relative;

    &:not(:last-child) {
      padding-bottom: 6rem;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        height: 1px;
        background: radial-gradient(circle, rgba(0,0,0,.01) 0%, rgba(255,255,255,1) 0%, rgba(0,0,0,.01) 100%);
      }
    }

  }

  &__title {
    font-weight: var(--fw-400);
  }

  &__heading {
    max-width: 47rem;
    margin-bottom: 3rem;
  }
  &__img {
    max-width: 24.5rem;
    width: 145px;
    height: 145px;
  }

  &__more {
    display: flex;
    gap: 3.5rem;
    align-items: center;
  }
  &__btn {
    display: flex;
    align-items: center;
    gap: .5rem;
    max-width: 242px;
    font-size: var(--fz-14);
    text-decoration: underline;
    padding: 9px 12px;
    border-radius: 6px;

    &--mob {
      display: none;
    }

    svg {
      width: 25px;
      height: 25px;
    }
  }
  &__info {
    color: #D3D3D3;
    display: inline;
  }
  &__result {
    font-size: var(--fz-28);
    font-weight: var(--fw-600);
    margin-bottom: 1.5rem;
    color: var(--clr-white);
  }
  &__text {
    color: var(--clr-white);
    max-width: 37rem;
    line-height: 1.3em;
  }

  &__description {
    background-color: #101114;
    border: 1px solid #212325;
    border-radius: 18px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    flex-grow: 1;
    justify-content: space-between;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }

    .program-part__block {
      background-color: var(--clr-dark-blue);
      position: relative;
      border-radius: 17px;
      max-width: 45rem;
      width: 100%;
      padding: 6rem 3rem 5rem 5.5rem;
    }
  }
  &__lesson {
    padding: 4.5rem 0 4.5rem 5.5rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    color: var(--clr-white);

    h3 {
      font-weight: var(--fw-400);
      max-width: 56rem;

      b {
        font-weight: var(--fw-600);
      }
    }
  }

  &__plan {
    text-transform: uppercase;
    color: #B4B4B4;
  }

  &__item {
    display: list-item;
    margin-bottom: .8rem;
    margin-left: 2rem;
    line-height: 1.2em;
  }

  &__tag {
    position: absolute;
    bottom: 30px;
    right: 50px;
    color: #3C5968;
  }

  &__show {
    display: flex;
    gap: 1rem;
    align-items: center;
    width: fit-content;
    text-transform: uppercase;
    color: var(--clr-white);

    &--mob {
      display: none;
      margin-top: 2rem;
    }

    span {
      border-bottom: 1px solid var(--clr-white);

      &:hover {
        border-bottom-style: dashed;
      }
    }

    svg {
      min-width: 20px;
      width: 20px;
      height: 20px;
    }
  }
}

.program-modul {
  display: none;
  padding: 4.5rem 8rem 7rem 10rem;
  background: rgba(239, 249, 255, 0.84);
  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(50px);
  border-radius: 27px;
  position: absolute;
  top: 0;
  z-index: 100;

  &.active {
    display: block;
  }

  &__title {
    text-transform: uppercase;
    margin-bottom: 3rem;
    color: #A4BBC9;
    font-size: var(--fz-22);
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 8rem;
    margin-bottom: 5.5rem;
  }
  &__item {
    max-width: 44rem;
    color: #151C21;
  }
  &__heading {
    margin-bottom: 2.5rem;
    color: #151C21;
    font-weight: var(--fw-600);
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: 65px;
      height: 2px;
      background-color: var(--clr-blue);
      top: -15px;
      left: 0;
    }
  }
  &__lesson {
    margin-bottom: 1.5rem;
    color: #878C90;
    text-transform: uppercase;
  }
  &__descr-list {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    margin-bottom: 2.5rem;
    margin-left: 2rem;

    &--mobile {
      display: none;
    }
  }
  &__descr-item {
    display: list-item;
  }
  &__result {
    padding-left: 2rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 7px;
      left: 6px;
      background-color: #000;
      width: 2px;
      height: 2px;
    }

    b {
      font-weight: var(--fw-600);
    }

    &:not(:last-of-type) {
      margin-bottom: .7rem;
    }
  }
  &__btn {
    background-color: #2A2E30;
    margin-inline: auto;
  }

  &__wrapp {
    display: flex;
    align-items: center;
    gap: 2.5rem;
    margin-bottom: 3rem;
  }

  &__img {
    width: 58px;
    height: 58px;
    border-radius: 50px;

    img {
      border-radius: 50px;
    }
  }

  &__block {
    .program-modul__heading {
      margin-bottom: 1rem;
    }
    .program-modul__lesson  {
      margin: 0;
    }
  }

  .cross {
    position: absolute;
    width: 35px;
    height: 35px;
    top: 25px;
    right: 55px;

    &::before,
    &:after {
      content: '';
      position: absolute;
      top: 15px;
      background-color: var(--clr-dark-blue);
      width: 35px;
      height: 4px;
      cursor: pointer;
    }
    &::before {
      transform: rotate(45deg);
    }

    &:after {
      transform: rotate(-45deg);
    }
  }
}


@include breakpoint-down(desktop) {

  .program__title {
    margin-bottom: 7rem;
  }
  .program-part__lesson {
    padding: 3rem;

    h3 {
      max-width: 45rem;
    }
  }

  .program-part__item {
    max-width: 45rem;
  }

  .program-part__wrapp {
    flex-direction: column;
    align-content: center;
    gap: 2rem;
  }

  .program-part__description {
    gap: 0;
    .program-part__block {
      padding: 3rem ;
    }
  }

  .program-modul {
    padding: 3rem 4rem;

    &__list {
      gap: 4rem;
    }

    .cross {
      right: 20px;
    }
  }

  .program-part__dropdown {
    max-width: 45rem;
  }
  .program-part__more {
    margin: 0;
  }

  .program-part__description {
    .program-part__tag {
      bottom: unset;
      top: 15px;
    }
  }

  .program-part__btn {
    &--mob {
      display: flex;
      margin-top: 2rem;
    }

    &--desk {
      display: none;
    }
  }
}

@include breakpoint-down(tablet-wide) {

  .program__title {
    margin-bottom: 5rem;
  }

  .program-part__description {
    flex-direction: column;
  }

  .program-part__lesson {
    padding: 3rem;
  }

}

@include breakpoint-down(tablet) {

  .program-part {
    gap: 4rem;
  }

  .program__title {
    margin-bottom: 4rem;
  }

  .program-part__more {
    flex-direction: column;
    align-items: flex-start;
    margin: 0;
  }

}

@include breakpoint-down(tablet-small) {

  .program-part__img {
    position: absolute;
    top: 0;
    right: -130px;
    width: 250px;
    height: 200px;
    z-index: -1;
  }

  .program-part__show {
    &--desk {
      display: none;
    }

    &--mob {
      display: flex;
    }
  }

  .program-part__tag {
    display: none;
  }

  .program-part__description {
    gap: 0;
    background: linear-gradient(168.72deg, #101114 -63.07%, #0E2D3B 107.31%);
    padding: 3rem 2rem;

    .program-part__block {
      padding: 0;
      margin-top: 2rem;
      background-color: transparent;
      width: 100%;
    }
  }

  .program-part__lesson {
    padding: 0;
  }

  .program-modul {
    padding: 5rem 2rem 3rem;
    left: 0;
    right: 0;

  }

}

@include breakpoint-down(phablet) {
  .program__title {
    font-size: 1.8rem;
  }

  .program-part__wrapp {
    align-content: flex-start;
  }
  .program-part__bonus,
  .program-part__text,
  .program-part__lesson {
    br {
      display: none;
    }
  }

  .program-modul__img {
    display: none;
  }

  .program-modul__descr-list  {
    &--mobile {
      display: flex;
    }
  }

  .program-modul__wrapp {
    flex-direction: column;
    align-items: flex-start;
  }
}

@include breakpoint-down(phone-wide) {


}
