@use '../additional/mixins' as *;

.hero {
  background-image: url('../img/hero-bg.jpg');
  background-color: #07090b;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top ;
  min-height: 100vh;
  color: var(--clr-white);

  &__inner {
    position: relative;
  }
  &__title {
    background: linear-gradient(102.76deg, #faf6ff 36.78%, rgba(250, 246, 255, 0) 125.23%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-size: var(--fz-80);
    text-transform: uppercase;
    margin-bottom: 6.5rem;

    span {
      font-size: var(--fz-110);
      font-weight: var(--fw-500);
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    max-width: 40rem;
    margin-bottom: 6.5rem;
  }

  &__text {
    position: relative;
    padding-left: 6rem;
    color: #D5E0E3;
    line-height: 1.3em;

    .indent {
      display: none;
    }

    &::before {
      content: '';
      position: absolute;
      top: .5em;
      left: 0;
      width: 40px;
      height: 1px;
      background-color: var(--clr-blue);
    }

    b {
      font-weight: var(--fw-600);
    }
  }

  &__floating {
    position: absolute;
    @include adaptivValue('right', 450, 0, 1165, 540);
    top: 70%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    background-color: rgba(0, 0, 0, .85);
    font-size: 12px;
    border-radius: 15px;
    transform: rotate(-5.94deg);
    max-width: 24rem;
    padding: 1.5rem 2.5rem;
    height: fit-content;

    span {
      font-size: var(--fz-24);
    }
  }
}

@include breakpoint-down(desktop-wide) {
  .hero {
    background-position: left -200px top 0;
  }
}

@include breakpoint-down(desktop) {
  .hero {
    background-position: left -300px top 0;
  }
}

@include breakpoint-down(tablet-wide) {
  .hero {
    background-position: left -400px top 0;
  }
}

@include breakpoint-down(tablet) {
  .hero {
    background-position: left -500px top 0;
  }
}

@include breakpoint-down(tablet-small) {
  .hero {
    background-position: left -600px top 0;
  }
}

@include breakpoint-down(phablet) {
  .hero {
    min-height: unset;
    background-image: url('../img/hero-sm-bg.jpg');
    background-position: center;

    &__title {
      margin-bottom: 4rem;
    }

    &__list {
      margin-bottom: 20rem;
    }

    &__text {
      max-width: 24rem;
      padding-left: 2rem;

      &::before {
        width: 13px;
      }
      .indent {
        display: block;
      }
    }

    &__floating {
      left: 20px;
      right: unset;
      gap: .5rem;
    }

    &__btn {
      margin-inline: auto;
      max-width: 20rem;
      font-size: 12px;
    }
  }
}
