@use '../additional/mixins' as *;

.footer {
  background-color: #162227;
  color: var(--clr-white);
  padding: 4rem 0;
  margin-inline: auto;
  width: 100%;

  &__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 1rem;
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    max-width: 31.5rem;
    width: 100%;
    font-size: 1.2rem;

    &:nth-child(2) {
      a {
        display: inline;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    &:last-of-type {
      font-size: var(--fz-16);
    }
  }

  &__link {
    text-decoration: underline;
    font-weight: var(--fw-700);
    font-size: var(--fz-20);

    &:not(:last-child) {
      margin-bottom: 3rem;
    }

    &:hover {
      text-decoration-style: dashed;
    }
  }

  &__payments {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 2.5rem;
    margin-top: 2rem;
  }

  &__icon {
    max-width: 143px;
    img {
      object-fit: contain;
    }
  }
}

@include breakpoint-down(tablet-wide) {
  .footer {

    &__inner {
      justify-content: center;
      text-align: center;
    }
    &__payments {
      justify-content: center;
    }

    &__block {
      max-width: 40rem;
    }
  }
}

@include breakpoint-down(tablet) {

  .footer {
    &__inner {
      justify-content: center;
    }

    &__block {
      address {
        br {
          display: none;
        }
      }
    }
  }

}
