@use '../additional/mixins' as *;

.reputation {
  background: url('../img/reputation-bg.png') no-repeat center / cover;

  &__wrapp {
    display: flex;
    align-items: center;
    gap: 12rem;
  }

  &__inner {
    position: relative;

    &:after {
      content: 'РАБОТАЙ ГОЛОВОЙ';
      position: absolute;
      bottom: 0;
      right: 0;
      opacity: .25;
      text-transform: uppercase;
      font-size: var(--fz-14);
      font-weight: var(--fw-100);
      color: var(--clr-white);
    }
  }

  &__img {
    max-width: 37rem;
  }

  &__title {
    max-width: 78rem;
    margin-bottom: 2.5rem;
  }
  &__subtitle {
    margin-bottom: 8rem;
    font-size: var(--fz-24);
    color: #E4E4E4;

    span {
      display: inline-block;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        bottom: -10px;
        right: -30px;
        width: 175px;
        height: 3px;
        border-radius: 50px;
        transform: rotate(-0.82deg);
        background-color: var(--clr-blue);
      }
    }
  }
  &__quote {
    display: block;
    padding: 4rem 2.5rem 4rem 3rem;
    border-radius: 13px;
    max-width: 65rem;
    color: #95909A;
    line-height: 1.3em;
    margin-bottom: 8.5rem;
    position: relative;
    z-index: 2;

    br {
      display: none;
    }

    &::before {
      content: url('../img/svg/icons/quotes.svg');
      position: absolute;
      top: -15px;
      left: 18px;
      z-index: 1;
    }

    &::after {
      content: '';
      position: absolute;
      inset: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background: rgba(39, 44, 51, 0.42);
      backdrop-filter: blur(70px);
      -webkit-backdrop-filter: blur(70px);
      border-radius: 13px;
    }
  }
}


@include breakpoint-down(desktop) {
  .reputation {
    &__img {
      position: absolute;
      right: 0;
      opacity: .4;
    }

    &__subtitle {
      max-width: 40rem;
      span {
        &::before {
          display: none;
        }
      }
    }
  }
}

@include breakpoint-down(tablet-wide) {
}

@include breakpoint-down(tablet) {
  .reputation__img {
    max-width: 30rem;
    top: -150px;
    opacity: .25;
  }
}

@include breakpoint-down(tablet-small) {
  .reputation__inner::after {
    display: none;
  }
  .reputation__btn {
    margin-inline: auto;
  }

  .reputation__quote {
    .indent {
      display: none;
    }
  }
}

@include breakpoint-down(phablet) {
  .reputation {
    &__quote {
      margin-bottom: 4rem;

      br {
        display: block ;
      }
    }
  }
}
