@use '../additional/mixins' as *;

.advantages {
  background: url('../img/advantages-bg.png') no-repeat center / cover;
  position: relative;
  padding-top: 15rem;

  &__content {
    position: relative;
    padding: 30px 30px 0 30px;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      right: -80px;
      z-index: 1;
      background-image: url('../img/advantages-img.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: top -200px right;
      width: 60rem;
      height: 67.5rem;
    }
  }

  &__inner {
    border-radius: 25px;
    padding: 7rem 10rem;
    position: relative;
    z-index: 1;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background: linear-gradient(282.66deg, rgba(39, 182, 243, 0.1) -22.41%, rgba(39, 182, 243, 0) 50.53%), rgba(23, 26, 29, 0.5);
      backdrop-filter: blur(70px);
      -webkit-backdrop-filter: blur(70px);
      border-radius: 25px;
    }


  }
  &__title {
    margin-bottom: 6rem;
    max-width: 71rem;
    font-size: var(--fz-42);
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 65rem;
    gap: 3.5rem 0;
  }
  &__item {
    display: flex;
    gap: 2.5rem;

    &:nth-child(even) {
      transform: translateY(35px);
    }
  }

  &__icon {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #1E2930;
    border-radius: 3px;

    svg {
      min-width: 35px;
      height: 35px;
    }
  }

  &__nums {
    margin-bottom: 1rem;
    font-size: var(--fz-22);
    font-weight: var(--fw-700);
    color: #E9E9E9;
  }
  &__text {
    max-width: 21rem;
    color: #E9E9E9;
  }
}


@include breakpoint-down(desktop) {

  .advantages__title {
    text-align: center;
    margin-inline: auto;
  }
  .advantages__content::after {
    display: none;
  }

  .advantages__list {
    margin: 0 auto;
    gap: 3.5rem 2rem;
  }
}

@include breakpoint-down(tablet-wide) {
  .advantages__inner {
    padding: 5rem 7rem;
  }
}

@include breakpoint-down(tablet) {
  .advantages__list {
    grid-template-columns: 1fr 1fr;

  }

  .advantages__content {
    padding: 0;
  }

  .advantages__inner {
    padding: 3em 5rem;

  }

  .advantages__item:nth-child(even) {
    transform: none;
  }
}

@include breakpoint-down(tablet-small) {
  .advantages {
    background: #181F23;
    background-image: url('../img/advantages-sm-bg.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
  .advantages__inner {
    padding: 3rem 4rem;

    &::before {
      display: none;
    }
  }

  .advantages__list {
    grid-template-columns: 1fr;
  }
}

@include breakpoint-down(phablet) {
  .advantages {
    background-position: left 50px center;
  }
  .advantages__inner {
    padding: 0;
    width: 100%;
  }

  .advantages__text {
    max-width: 100%;
  }
}
