@use '../additional/mixins' as *;

.faq {
  background-color: #07090B;
  color: var(--clr-white);
  background-image: url('../img/faq-bg.png');
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__title {
    margin-bottom: 10rem;
    text-align: center;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 7rem;
  }

  &__list {
    display: flex;
    justify-content: center;
    gap: 7rem;
    margin-bottom: 10rem;
  }
  &__item {
    max-width: 50rem;
    width: 100%;
    padding-bottom: 4rem;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 10px;
      width: 500px;
      height: 1px;
      background: radial-gradient(circle, transparent 0%, rgba(255,255,255,1) 0%, transparent 100%);
    }

  }
  &__wrapp {
    display: flex;
    align-items: center;
    gap: 2rem;

    &:hover {
      cursor: pointer;
    }

    &._active {
      .faq__icon {
        transform: rotate(45deg);
      }
    }


  }

  &__icon {
    min-width: 60px;
    height: 60px;
    background-color: #181B1F;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .1s linear;
  }
  &__cross {
    width: 26px;
    height: 26px;
  }
  &__questions {
    font-size: var(--fz-28);
  }
  &__answer {
    padding-top: 4rem;
    position: relative;

    p {
      line-height: 1.3em;

      span {
        margin-top: 1rem;
      }
    }

  }
  &__btn {
    margin-inline: auto;
  }
}

@include breakpoint-down(desktop) {
  .faq {

    &__content {
      gap: 5rem;
    }
  }
}

@include breakpoint-down(tablet-wide) {
  .faq {

    &__list {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 7rem;
      gap: 5rem;
    }

    &__item {
      display: flex;
      flex-direction: column;
    }

    &__wrapp {
      justify-content: space-between;
      align-items: center;
    }

    &__icon {
      order: 1;
    }
  }
}

@include breakpoint-down(tablet) {
  .faq__title {
    margin-bottom: 7rem;
  }
}

@include breakpoint-down(tablet-small) {
  .faq__title {
    margin-bottom: 5rem;
  }
  .faq__item::after {
    max-width: 90%;
  }
}

@include breakpoint-down(phablet) {
  .faq {
    &__title {
      margin-bottom: 4rem;
    }
    &__list {
      margin-bottom: 4rem;
      gap: 1rem;
    }

    &__icon {
      min-width: 36px;
      height: 36px;

      svg {
        width: 15px;
        height: 15px;
      }
    }

    &__answer {
      padding-top: 2rem;
    }

    &__content {
      gap: 1rem;
    }

    &__item {
      padding-bottom: 2rem;
      &::after {
        left: -120px;
        max-width: 100%;
      }
    }

  }

}
