@use '../additional/mixins' as *;

.proofs {
  background-color: #07090b;
  background-image: url('../img/proofs-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__wrapp {
    position: relative;
  }

  &__title {
    margin-bottom: 8rem;
    text-align: center;
    color: var(--clr-white);
  }

  &__subtitle {
    display: none;
    font-size: var(--fz-14);
    color: var(--clr-white);
    margin-bottom: 2.5rem;
  }

  &__slider {
    position: static;
    max-width: 1125px;
    margin-bottom: 7.5rem;
    padding-bottom: 7.5rem;
  }

  &__list {
   max-width: calc(100vw - 30px);
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5rem 2rem 4rem 4.5rem;
    background-color: #1b1b1b;
    border-radius: 13px;
    min-height: 325px;
  }

  &__block {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }

  &__avatar {
    border-radius: 19px;
    width: 137px;
    height: 137px;
    object-fit: cover;
  }

  &__name {
    font-family: var(--ff-2);
    font-size: var(--fz-28);
    font-weight: var(--fw-500);
    color: var(--clr-white);
    text-transform: uppercase;
    margin-bottom: 1.5rem;
  }

  &__result {
    margin-bottom: 1.5rem;
    color: #585858;
  }

  &__points {
    display: flex;
    gap: 2rem;
    color: var(--clr-white);
    margin-bottom: 2rem;

    span {
      font-size: var(--fz-14);
      color: inherit;
      text-transform: uppercase;
    }
  }

  &__text {
    color: #c0c0c0;
    max-width: 32rem;
  }

  &__links {
    display: flex;
    gap: 4rem;
  }

  &__link {
    max-width: 20rem;
    width: 100%;
    font-size: var(--fz-16);
    margin-top: auto;
  }

  &__btn {
    font-size: var(--fz-16);
    margin-inline: auto;

    &-2 {
      border-color: var(--clr-white);
      color: var(--clr-white);
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    top: 40%;
    width: 47px;
    height: 47px;
    background-color: #535353;
    border-radius: 100vmax;
    transition: scale .05s linear;

    &::after {
      display: none;
    }

    &:active {
    scale: .9;

    }

    svg {
      width: 20px;
      height: 15px;
      fill: var(--clr-white);
    }
  }

  .swiper-button-next {
    right: -5px;
  }

  .swiper-button-prev {
    left: -5px;

    svg {
      rotate: 180deg;
    }
  }

  .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .swiper-pagination-bullet {
    width: 13px;
    height: 13px;
    background-color: #424242;
    opacity: 1;

    &-active {
      background-color: var(--clr-blue);
    }
  }
}


@include breakpoint-down(desktop) {
  .proofs {
    &__item {
      padding: 3rem 2rem;
    }
  }
}

@include breakpoint-down(tablet) {
  .proofs {
    &__item {
      min-height: 275px;
    }
  }
}


@include breakpoint-down(phablet) {
  .proofs {
    &__slider {
      padding-bottom: unset;
      padding-top: 6.5rem;
      margin-bottom: 3rem;
      &::before {
        content: 'ЛИСТАЙ';
        position: absolute;
        top: 1.5rem;
        left: 0;
        display: block;
        text-transform: uppercase;
        font-size: 10px;
        font-weight: var(--fw-300);
        line-height: 1.3em;
        color: var(--clr-white);
        padding: 7px 35px 7px 0;
        background-image: url('../img/svg/icons/hand.svg');
        background-repeat: no-repeat;
        background-size: 25px 25px;
        background-position: center right;
      }
    }
    &__title {
      margin-bottom: 1.5rem;
      text-align: left;
    }
    &__subtitle {
      display: block;
      line-height: 1.3em;
      margin-bottom: 0rem;
    }
    &__avatar {
      width: 90px;
      height: 90px;
    }

    &__item {
      padding: 2.5rem 3rem;
      min-height: 245px;
    }

    &__name {
      margin-bottom: .8rem;
    }

    &__text {
      font-size: 10px;
    }

    &__result {
      margin-bottom: .5rem;
    }

    &__arrow{
      display: none;
    }

    &__link {
      padding: 9px 12px;
    }

    .swiper-pagination {
      top: 2rem;
      right: 0;
      width: min-content;
      left: unset;
      bottom: unset;
    }
  }
}

@include breakpoint-down(phone-wide) {
  .proofs {
    &__slider {
      margin-bottom: 4rem;
    }
    &__item {
      padding: 2rem;
      min-height: 200px;
    }

    &__text {
      max-width: 20rem;
    }


  }
}


@include breakpoint-down(phone) {
  .proofs {

    &__item {
      min-height: unset;
      max-height: 175px;
    }
  }
}
