@use '../additional/mixins' as *;

.result {
  background-image: url('../img/result-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  position: relative;

  &__inner {
    margin-bottom: 9rem;
  }

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    width: 100%;
    background-image:
    url('../img/result-mask.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: left;
    background-color: rgba(#000, .97) ;
  }

  &__first {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      background-image: url('../img/result-1.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: bottom right;
      height: 100%;
      width: 100%;
    }
  }

  &__title {
    margin-bottom: 7.5rem;
    text-align: center;
    margin-inline: auto;
    max-width: 76rem;
    color: var(--clr-white);
    position: relative;
  }
  &__inner {
    display: flex;
    justify-content: center;
    position: relative;
  }
  &__main {
    max-width: 26rem;
    width: 100%;
    min-height: 47.5rem;
    padding: 3.5rem 2.5rem;
    color: var(--clr-white);
    background-color: #0F1A1F;
    border-radius: 15px;
    position: relative;

    &::before {
      content: attr(count);
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 367px;
      line-height: 186px;
      font-weight: var(--fw-900);
      color: rgba(39, 182, 243, 0.02);
    }

    img {
      height: 260px;
    }
  }

  &__first {
    padding-right: 4rem;
    position: relative;
  }

  &__heading {
    margin-bottom: 2rem;
  }
  &__text {
    color: #D9D9D9;

    b {
      color: var(--clr-white);
      font-weight: var(--fw-600);
    }
  }
  &__wrapp {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    gap: 3rem;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    border-radius: 15px;
    padding-left: 1rem;
    background-color: #0F1A1F;
    width: 100%;
    position: relative;

    &:nth-child(1) {
      display: none;
    }

    &::before {
      content: attr(count);
      position: absolute;
      bottom: 0;
      font-size: 184px;
      line-height: 100px;
      font-weight: var(--fw-900);
      color: rgba(39, 182, 243, 0.02);
    }

    &:nth-child(3) {
      img {
        max-width: 125px;
      }
    }
  }
  &__img {
    max-width: 120px;
    height: 100%;
  }
  &__descr {
    color: #D9D9D9;
    max-width: 29rem;
    line-height: 1.3em;
    br {
      display: none;
    }

    b {
      display: block;
      color: var(--clr-white);
    }
  }

  &__btn {
    margin-inline: auto;
    position: relative;
  }
}



@include breakpoint-down(tablet) {
  .result {
    padding-top: 100px !important;
    &__first {
      display: none;
    }

    &__title {
      margin-bottom: 5rem;
    }

    &__item {
      background-color: transparent;
      display: grid;
      grid-template-columns: min-content 1fr;
      padding-left: 0;

      &:nth-child(1) {
        display: grid;
      }
      &::before {
        display: none;
      }
    }

    &__wrapp {
      grid-template-rows: repeat(4, 1fr);
      gap: 1.5rem;
    }

    &__img {
      min-width: 100px;
    }

    &__descr {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      font-size: 12px;

      b {
        font-size: 18px;
      }

      .indent {
        display: none;
      }
    }

    &__inner {
      margin-bottom: 5rem;
    }
  }
}

@include breakpoint-down(tablet-small) {

  .result {
    &__title {
      margin-bottom: 4rem;
    }
    &__item::before {
      display: none;
    }
    &__descr {
      br {
        display: inline;
      }


    }

  }
}

@include breakpoint-down(phablet) {
  .result {
    &__title {
      margin-bottom: 3rem;
    }

    &__descr {
      br {
        display: block;
      }
    }

    &__inner {
      margin-bottom: 3.5rem;
    }

  }
}
