@use 'sass:color';
@use 'mixins' as *;
@use 'colors' as *;
@use 'font-styles' as *;
@use 'z-index' as *;

:root {
  --main-fz: 62.5%;
  --ff-1: 'MacPaw Fixel Display', sans-serif;
  --ff-2: 'Druk Wide Cyr', sans-serif;
  --ff-3: 'Druk Text Wide', sans-serif;
  --container: 1165px;

  @each $name, $value in $colors {
    --clr-#{$name}: #{$value};
    // --clr-#{$name}-0: #{$value};
    // --clr-#{$name}-1: #{color.adjust($value, $lightness: 15%)};
    // --clr-#{$name}-2: #{color.adjust($value, $lightness: 30%)};
    // --clr-#{$name}-3: #{color.adjust($value, $lightness: 45%)};
    // --clr-#{$name}-4: #{color.adjust($value, $lightness: 60%)};
  }

  @each $screen-size, $font-size in $font-sizes {
    @if $screen-size == desktop-wide {
      @each $size, $value in $font-size {
        --fz-#{$size}: #{$value};
      }
    } @else if $screen-size == desktop {
      @include breakpoint-down(desktop) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 1199.98px;
      }
    } @else if $screen-size == tablet-wide {
      @include breakpoint-down(tablet-wide) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 991.98px;
      }
    } @else if $screen-size == tablet {
      @include breakpoint-down(tablet) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 767.98px;
      }
    } @else if $screen-size == tablet-small {
      @include breakpoint-down(tablet-small) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 575.98px;
      }
    } @else if $screen-size == phablet {
      @include breakpoint-down(phablet) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 480.98px;
      }
    } @else if $screen-size == phone-wide {
      @include breakpoint-down(phone-wide) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 375.98px;
      }
    } @else if $screen-size == phone {
      @include breakpoint-down(phone) {
        @each $size, $value in $font-size {
          --fz-#{$size}: #{$value};
        }
        --container: 320.98px;
      }
    }
  }

  @each $value, $weight in $font-weights {
    --fw-#{$weight}: #{$value};
  }

  @each $index, $height in $z-index {
    --i-#{$index}: #{$height};
  }
}
