$breakpoints-down: (
  "desktop-wide": "1399.98px", //1399.98px
  "desktop":      "1199.98px", //1199.98px
  "tablet-wide":  "991.98px",  //991.98px
  "tablet":       "767.98px",  //767.98px
  "tablet-small": "575.98px",  //575.98px
  "phablet":      "480.98px",  //480.98px
  "phone-wide":   "375.98px",  //375.98px
  "phone":        "320.98",    //320.98px
);

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}


@mixin adaptivValue($property, $startSize, $minSize, $maxWidth, $minWidth) {
  $addSize: $startSize - $minSize;

  #{$property}: $startSize + px;

  @media (max-width: #{$maxWidth + px}) {
    #{$property}: calc(
      #{$minSize + px} + #{$addSize} * ((100vw - #{$minWidth + px}) / #{$maxWidth - $minWidth})
    );
  }
  @media (max-width: #{$minWidth + px}) {
    #{$property}: $minSize + px;
  }
}
