@use '../additional/mixins' as *;

.btn {
  display: block;
  max-width: 28rem;
  width: 100%;
  font-size: var(--fz-18);
  line-height: 1.3em;
  background-color: var(--clr-light-blue);
  padding: 1.8rem 1rem;
  text-transform: uppercase;
  color: var(--clr-white);
  border-radius: 10px;
  letter-spacing: 1px;
  text-align: center;
  transition: 0.1s linear;

  &:hover {
    background-color: #68D2FF;
    box-shadow: 0px 0px 70px rgba(12, 144, 200, 0.25);
  }

  &:active {
    transform: scale(0.95);
  }
}

@include breakpoint-down(phone-wide) {
  .btn {
    max-width: 100%;
    font-size: 12px;
  }
}
