@use '../additional/mixins' as *;

.speakers {
  background-color: rgba(13, 31, 39, 0.9);
  background-image: url('../img/speakers-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__title {
    margin-bottom: 9rem;
    color: var(--clr-white);
  }
  &__wrapp {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4rem;
  }
  &__card {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4rem;
    border-radius: 22px;
    max-width: 56rem;
    width: 100%;
    padding: 4.7rem 2rem;
    color: #E8E8E8;
    isolation: isolate;
    overflow: hidden;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      z-index: -1;
      filter: blur(35px);
      width: 100%;
      height: 100%;
      background-image: url('../img/bg.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }

    &::after {
      content: '';
      position: absolute;
      inset: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-color: rgba(13, 31, 39, 0.85);
    }
  }
  &__img {
    min-width: 169px;
    max-width: 169px;
    max-height: 169px;
    border-radius: 100px;
    background-color: #3C6D82;
    overflow: hidden;

    img {
      object-fit: cover;
    }
  }
  &__name {
    margin-bottom: 2.5rem;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: .8rem;
    max-width: 28rem;
  }
  &__item {
    font-size: var(--fz-14);
    line-height: 1.3em;
    padding-left: 1.5rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 5px;
      top: .5em;
      width: 1px;
      height: 1px;
      background-color: var(--clr-white);
    }
  }

  &__text {
    font-size: var(--fz-14);
    margin-bottom: 3rem;
    padding-left: 1.5rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: -13px;
      background: radial-gradient(41.55% 41.55% at 50% 50%, #27B6F3 0%, rgba(39, 182, 243, 0) 100%);
      border-radius: 30px;
      width: 3px;
      height: 66px;
    }
  }
}


@include breakpoint-down(tablet-small) {

  .speakers {

    &__title {
      margin-bottom: 3rem;
    }

    &__wrapp {
      gap: 1rem;
    }

    &__card {
      align-items: flex-start;
      gap: 2rem;
    }
    &__img {
      min-width: 78px;
      max-width: 78px;
      height: 78px;
    }

    &__name {
      margin-bottom: 2rem;
    }

  }
}

@include breakpoint-down(phablet) {
  .speakers {
    width: 100%;

    &__card {
      padding: 2.4rem 1.8rem;
      align-items: flex-start;
      gap: 2rem;
    }

  }
}
