@use 'additional/mixins' as *;

.modal {
  color: var(--clr-white);
  position: fixed;
  z-index: 1000;
  inset: 0;
  bottom: -100%;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: .2s linear;

  &.active {
    display: flex;
    visibility: visible;
    bottom: 0;
  }

  &-cross {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 35px;
    height: 35px;
    cursor: pointer;
    transition: .2s linear;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 15px;
      left: 0;
      background-color: var(--clr-white);
      width: 35px;
      height: 4px;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    &:hover {
      transform: rotate(90deg);
    }
  }

  &__content {
    background: rgba(44, 60, 67, 0.77);
    backdrop-filter: blur(50px);
    -webkit-backdrop-filter: blur(50px);
    padding: 5.5rem 8rem;
    max-width: 73rem;
    border-radius: 25px;
    margin: 0 auto;
    width: 100%;
    position: relative;
    z-index: 2;
  }
  &__heading {
    font-size: var(--fz-28);
    margin-bottom: 3.5rem;
    line-height: 1.3em;
  }
  &__text {
    font-size: var(--fz-18);
    margin-bottom: 5rem;
    line-height: 1.3em;
  }
  &__form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    gap:5.5rem 4rem ;
  }
  &__label {
    width: 100%;
    max-width: 24rem;
    color: #A1B4BC;
    font-size: var(--fz-18);
    font-weight: var(--fw-300);

    span {
      margin-bottom: 2rem;

      span {
        font-size: var(--fz-14);
      }
    }
  }
  &__input {
    background-color: #4E646C;
    border-radius: 3px;
    max-width: 24rem;
    width: 100%;
    padding: .8rem 1.2rem;
    color: var(--clr-white);
    border: 1px solid transparent;
    outline: none;
    transition: .1s linear;

    &:hover {
      border: 1px solid #A1B4BC;
    }

    &::placeholder {
      color: #A1B4BC;
    }
  }

  &__btn {
    display: flex;
    gap: 1rem ;
    align-items: center;
    justify-content: center;
    height: 55px;
    max-width: 24rem;

    &:disabled {
      background-color: rgb(140, 190, 214);
    }

    svg {
      width:20px;
      height: 20px;
    }
  }
}
.overflow {
  position: fixed;
  inset: 0;
  z-index: 1;
  height: 100%;
  width: 100%;
  cursor: pointer;
  background-color: rgba(#000,.7)
}

@include breakpoint-down(tablet) {
  .modal {

    &__content {
      padding: 3rem 5rem;
    }
    &__form {
      gap: 3rem;
      flex-direction: column;
      align-items: center;
    }
    &__label,
    &__input {
      max-width: 30rem;
    }
  }
}


@include breakpoint-down(tablet-small) {
  .modal {
    &__form {
      justify-content: center;
      gap: 2rem;
    }

    &__content {
      padding: 3rem;
    }

    &__heading {
      margin-bottom: 2rem;
    }

    &__text {
      margin-bottom: 3rem;
    }
    &__label,
    &__input {
      max-width: 100%;
    }
    &__label {
      span {
        margin-bottom: .5rem;
      }
    }
    &__btn {
      max-width: 100%;
    }

    &__btn {
      margin-top: 1rem;
    }
  }
}

@include breakpoint-down(phablet) {
  .modal {

  }
}

@include breakpoint-down(phone-wide) {
  .modal {
    &__content {
      padding: 2rem 3rem;
    }
  }
}
