@use 'additional/mixins' as *;

html {
  font-size: var(--main-fz);
}


body {
  font-family: var(--ff-1);
  font-size: var(--fz-16);
  font-weight: var(--fw-400);
  line-height: 1.2em;
  color: var(--clr-black);
  background-color: var(--clr-black);
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
}

.container {
  max-width: var(--container);
  width: min(calc(100% - 30px), var(--container));
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--ff-1);
  line-height: 1.2em;
  font-weight: var(--fw-500);
  color: var(--clr-white);
}

h1 {
  font-family: var(--ff-2);
  font-size: var(--fz-80);
}

h2 {
  font-family: var(--ff-2);
  font-size: var(--fz-35);
  text-transform: uppercase;
}

h3 {
  font-weight: var(--fw-700);
  font-size: var(--fz-28);
}

span {
  display: block;
  line-height: 1.2em;
}

p {
  line-height: 1.2em;
}

// * {
//   border: 1px solid red;
// }

section {
  --section-pd: 100px;
  --section-bonus-pd: 110px;
  position: relative;
  overflow: clip;
  @include adaptivValue ('--section-pd', 100, 50, 1165, 375);
  padding: var(--section-pd) 0;


  &:nth-child(2) {
    z-index: var(--i-high);
    @include adaptivValue ('border-bottom-left-radius', 110, 35, 1165, 375);
    @include adaptivValue ('border-bottom-right-radius', 110, 35, 1165, 375);
  }

  &:nth-child(9) {
    padding-top: calc(var(--section-pd) + var(--section-bonus-pd));
    @include adaptivValue ('margin-top', -110, -35, 1165, 375);
  }
}

@for $i from 3 through 8 {
  section:nth-child(#{$i}) {
    @include adaptivValue ('margin-top', -110, -35, 1165, 375);
    @include adaptivValue ('border-bottom-left-radius', 110, 35, 1165, 375);
    @include adaptivValue ('--section-bonus-pd', 110, 25, 1165, 375);
    @include adaptivValue ('border-bottom-right-radius', 110, 35, 1165, 375);
    z-index: calc(20 - #{$i});
    padding-top: calc(var(--section-pd) + var(--section-bonus-pd));
  }
}

